/* --------FORM-------- */

.form {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.input-container {
	position: relative;
	margin: 10px;
}

input {
	padding: 10px;
	font-family: 'Oxygen';
	font-size: 1em;
	border: none;
	border-bottom: solid 1px #949494;
	box-sizing: border-box;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

input:focus {
	outline: none;
}

.input-underline {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 0;
	height: 2px;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

input:focus ~ .input-underline {
	width: 100%;
	background-color: gray;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

input:valid ~ .input-underline {
	width: 100%;
	background-color: #768BB4;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.input-label {
	position: absolute;
	top: 12px;
	left: 10px;
    font-family: 'Oxygen';
    color: red;
    font-size: 20px;
    font-weight: bold;
	text-transform: capitalize;
	cursor: text;
	pointer-events: none;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

input:focus ~ .input-label {
	top: -13px;
	left: 10px;
	font-size: 1em;
	color: gray;
	font-weight: bold;
}

input:valid ~ .input-label {
	top: -13px;
	left: 10px;
	font-size: 1em;
	color: #768BB4;
	font-weight: bold;
}
